.user-item {
  margin: 1rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem;

  a {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    text-decoration: none;
    padding: 1rem;
    color: white;
    background: #292929;

    &:hover,
    &:active {
      background: #ffd900;
    }
  }

  .user-item__content {
    padding: 0 !important;

    .user-item__image {
      width: 4rem;
      height: 4rem;
      margin-right: 1rem;
    }

    .user-item__info {
      h2 {
        font-size: 1.5rem;
        margin: 0 0 0.5rem 0;
        font-weight: normal;
        color: #ffd900;
      }
      h3 {
        margin: 0;
      }
    }
  }
  &:hover,
  &:active {
    .user-item__info {
      h2,
      h3 {
        color: #292929 !important;
      }
    }
  }
}
